import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams,ViewController } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the MarketingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-addattendpage',
  templateUrl: 'addattend.html',
})
export class AddattendPage {
  addOthers:any={};
  addParams:any={};
  others:any={};
  infoType:string;
  num:number;
  isDisabled:boolean=true;
  date:any;
  activityType2:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,
   public httpService:HttpService,public alertCtrl: AlertController,
   public viewCtrl: ViewController) {
   this.infoType=this.navParams.get('type');
   this.num=this.navParams.get('num');

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad AddOtherPage');
  }

  saveOther(){
  	const URL="search/addReport.json";
  	const par={
  		"name":this.addOthers.title,
  		"type":this.addOthers.type,
  		"department":this.addOthers.department,
  		"position":this.addOthers.position,
  		"businessCard":this.addOthers.businessCard,
  		"phone":this.addOthers.phone
  	}
	this.httpService.getData({
        url: URL,
        body: par
    }).then(res => {
    	console.log(res);
      let result = res.status;
      if (result == 0) {
        this.showAlertMsg("",true);
        window.history.go(-1);
      }
      else {
        this.showAlertMsg( "",false);
      }
    }).catch(error => {
        this.showAlertMsg(error,false);
    })
  }

  transformData(data){
	const da=new Date(data).getDay();
  	switch (da){
  		case 0:
  			this.date="";
  			break;
  		case 1:
  			this.date="";
  			break;
  		case 2:
  			this.date="";
  			break;
  		case 3:
  			this.date="";
  			break;
  		case 4:
  			this.date="";
  			break;
  		case 5:
  			this.date="";
  			break;
  		case 6:
  			this.date="";
  			break;
  		default:
  			break;
  	}
  	return (data+" "+this.date);
  }
  saveInfo(URL){
  	this.httpService.getData({
        url: URL,
        body: {
//        params: this.addOthers
          params: this.addParams
        },
    }).then(res => {
    	console.log(res);
      let result = res.status;
      if (result == 0) {
        this.showAlertMsg("",true);
      }
      else {
        this.showAlertMsg( "",false);
      }
    }).catch(error => {
        this.showAlertMsg(error,false);
    })
  }
  showAlertMsg(messag,isTouter) {
    let alert = this.alertCtrl.create({
      title: '',
      subTitle: messag,
      buttons: [
      	{
      		text:"",
      		handler: () => {
		      console.log('Cancel clicked');

		     if(isTouter){
		     	if(this.infoType==""){
					this.dismiss(true);
			    }else{
					this.dismiss(true);
				}
		     }

		    }
      	}
      ]

    });
    alert.present();
  }
  dismiss(data) {
  	if(data){
  		this.viewCtrl.dismiss(this.addOthers);
  	}else{
  		console.log(this.others);
//		this.viewCtrl.dismiss(JSON.parse(this.others));
	 	if(this.infoType!=""){
	 		this.addOthers=this.others;
	 		this.viewCtrl.dismiss(this.addOthers);
	    }else{
	    	this.addOthers=JSON.parse(this.others);
	    	this.viewCtrl.dismiss(this.addOthers);
	    }

  	}

  }


   //
  switchType(){

  }
    //
  switchType2(){
//  console.log(this.meterial);
    console.log('');
  }

}



