import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';
import { ApprovedpendingPage } from '../approvedpending/approvedpending';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the ApproveindiadetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-approveindiadetail',
  templateUrl: 'approveindiadetail.html',
})
export class ApproveindiadetailPage {

  //
  indiaDetail:any={};
  //IDID
  processinstId:any;
  //IDID
  sealId:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,public alertCtrl: AlertController,private httpService:HttpService) {
    this.processinstId = navParams.get("processinstId");
    this.sealId = navParams.get("sealId");
    console.log("processinstId"+this.processinstId );
    console.log("sealId"+this.sealId );
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApproveindiadetailPage');
    this.getDetailPage();
  }

  //
  getDetailPage(){
    const url = "approveindialist/approveindiaDetail.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "sealId":this.sealId,
        "processinstId":this.processinstId
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.indiaDetail =res;
      console.log(this.indiaDetail);
    })

  }

  //
  submit(){
    //
    let confirm = this.alertCtrl.create({
      title: '',
      message: '?',
      buttons: [
        {
          text: '',
          handler: () => {
            console.log('');
          }
        },
        {
          text: '',
          handler: () => {
            //
            this.navCtrl.push(ApprovedpendingPage,{approve_index:"1"},{ animate: true });
            console.log('');
          }
        }
      ]
    });
    confirm.present();
  }

}



