import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {HttpService} from "../../providers/HttpService";

/**
 * Generated class for the BusinessreportdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-businessreportdetail',
  templateUrl: 'businessreportdetail.html',
})
export class BusinessreportdetailPage {

  //ID
  empId:any = 1;
  //
  startTime:any;
  //
  endTime:any;
  //
  pageDetail:any;
  //
  reprotArrListLastWeek:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private httpService:HttpService) {
    this.startTime = navParams.get("startTime");
    this.endTime = navParams.get("endTime");
    this.reprotArrListLastWeek = navParams.get("reprotArrListLastWeek");
    console.log("week:"+this.reprotArrListLastWeek);

    console.log(""+this.startTime + " " +this.endTime );
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BusinessreportdetailPage');
    this.getDetail();
  }

  //
  getDetail(){
    const url = "home_g/home_g.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";

    const data={
      "appId":"mcrm",
      "reqParam":{
        "userId":this.empId,
        "startTime":this.startTime,
        "endTime":this.endTime
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: {}
    }).then(res=>{
      this.pageDetail =res;
      console.log(this.pageDetail);
    })

  }

}



