import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApprovedpenddetailPage } from '../approvedpenddetail/approvedpenddetail';
import { ApproveprojectdetailPage } from '../approveprojectdetail/approveprojectdetail';
import { ApproveindiadetailPage } from '../approveindiadetail/approveindiadetail';
import { ApprovewechartdetailPage } from '../approvewechartdetail/approvewechartdetail';
import {HttpService} from "../../providers/HttpService";


/**
 * Generated class for the ApprovedpendingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-approvedpending',
  templateUrl: 'approvedpending.html',
})
export class ApprovedpendingPage {

  pet:any = "kittens"
  approve_index:any;
  //
  empId:any = "10000";
  //
  customName:any = "";
  //
  getapproveListbystyle:any;
  //ID
  processinstId:any;
  //ID
  wechartId:any;
  //ID
  sealId:any;
  //
  status:any;
  //()
  time:any;


  approvepend_Type:any="";

  select ={
    ismyListshow:false,
    isUnderListshow:false,
  }


  constructor(public navCtrl: NavController, public navParams: NavParams,public httpService:HttpService) {
    this.approve_index = navParams.get('approve_index');
    console.log("index"+this.approve_index);
    if(this.approve_index == 1){
      this.approvepend_Type="";
    }else if(this.approve_index == 2){
      this.approvepend_Type="";
    }else if(this.approve_index == 3){
      this.approvepend_Type="";
    }else if(this.approve_index == 4){
      this.approvepend_Type="";
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApprovedpendingPage');
    //
    //status,time
    this.time = "";
    this.status = "";
    this.getApproveList();
  }

  //
  changeselec () {
    this.select.ismyListshow = false;
    this.select.isUnderListshow = false
  }

  //
  approvedpendingdetail(index){
    if(index == 1){
      //
      this.navCtrl.push(ApprovedpenddetailPage,{processinstId:this.processinstId},
        { animate: true });
    }else if(index == 2){
      //
      this.navCtrl.push(ApproveprojectdetailPage,{processinstId:this.processinstId},
        { animate: true });
    }else if(index == 3){
      //
      this.navCtrl.push(ApproveindiadetailPage,{sealId:this.sealId,processinstId:this.processinstId},
        { animate: true });
    }else if(index == 4){
      //
      this.navCtrl.push(ApprovewechartdetailPage,{wechartId:this.wechartId,processinstId:this.processinstId},
        { animate: true });
    }

  }

  //
  getApproveList(){
    //json
    const url = "home_g/home_g.json";
    // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";
    const data={
      "appId":"mcrm",
      "reqParam":{
        "userId":this.empId,
        "customName":this.customName,
        "type":this.approvepend_Type,
        "status":this.status,
        "time":this.time,
        "page":{
          "firstPageNo":0,
          "dataCount":0,
          "pageNo":0
        }
      },
      "serviceId":"auth.mcrmLogin"
    }

    this.httpService.getData({
      url: url,
      body: data
    }).then(res=>{
      this.getapproveListbystyle =res;
      console.log(res);
      //IDID
      //IDID
      this.sealId = 1;
      this.wechartId = 1;
      //IDID
      this.processinstId = 1;
    })
  }


  //
  switch_Type(){
    console.log(":"+this.approvepend_Type);
    this.time = "";
    this.status = "";
    this.getApproveList();
  }

  //
  switchType(){
    console.log(""+this.time);
  }
  //
  switchType1(){
    console.log(""+this.status);
  }

  //
  getsearchlist(){
    console.log("");
    //
    this.getApproveList();
  }

}



