import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TabsPage } from "../tabs/tabs";
import {HttpService} from "../../providers/HttpService";
import {ToastController} from 'ionic-angular';
import {Contents} from '../../providers/Constants'
import { Cookie } from 'ng2-cookies/ng2-cookies';
import {escape, unescape} from "querystring";


/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  //
  notice:any = {};
  //checkbox
  login_get_checkbox:boolean=false;
  //
  login_username:any;
  //
  login_password:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,public httpService:HttpService,public toastCtrl:ToastController
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');

    //cookie,inputcheckboxtrue
    if(this.getCookie("cookie_password") || this.getCookie("cookie_username")){
      // this.login_username = Cookie.get("cookie_username");
      // this.login_password = Cookie.get("cookie_password");
      this.login_username = this.getCookie("cookie_username");
      this.login_password = this.getCookie("cookie_password");
      this.login_get_checkbox = true;
    }
  }

  //JScookie
  setCookie(name, value, Days){
    if(Days == null || Days == ''){
      Days = 300;
    }
    var exp  = new Date();
    exp.setTime(exp.getTime() + Days*24*60*60*1000);
    document.cookie = name + "="+ decodeURI(value) + "; path=/;expires=" + exp.toUTCString();
  }
  //JScookie
  getCookie(name) {
    var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
    if(arr=document.cookie.match(reg))
      return decodeURI(arr[2]);
    else
      return null;
  }
  //JScookie
  delCookie(name){
    var exp = new Date();
    exp.setTime(exp.getTime() - 1);
    var cval=this.getCookie(name);
    if(cval!=null)
      document.cookie= name + "="+cval+"; path=/;expires="+exp.toUTCString();
  }

  //
  logIn(username:HTMLInputElement ,password:HTMLInputElement) {
    //alert('');
    if (username.value.length == 0) {
        let toast = this.toastCtrl.create({
          message: '',//
          duration: 3000,//3
          position: 'middle'//
        })
      toast.present();
    }else if (password.value.length == 0) {
      let toast = this.toastCtrl.create({
        message: '',//
        duration: 3000,//3
        position: 'middle'//
      })
      toast.present();
    }else{
      //
      //cookiecookie
      if(this.login_get_checkbox){
        // Cookie.set('cookie_username', this.login_username);
        // Cookie.set('cookie_password', this.login_password);

        this.setCookie("cookie_username",this.login_username,15);
        this.setCookie("cookie_password",this.login_password,15);

         //console.log("cookie_username:"+Cookie.get("cookie_username"));
         //console.log("cookie_password:"+Cookie.get("cookie_password"));
        console.log("cookie_username"+this.getCookie("cookie_username"));
        console.log("cookie_password"+this.getCookie("cookie_password"));
        console.log("");
      }else{
        //cookie
         Cookie.set("cookie_username","");
         Cookie.set("cookie_password","");
        //this.delCookie("cookie_username");
        //this.delCookie("cookie_password");
        // console.log("cookie_username:"+Cookie.get("cookie_username"));
        // console.log("cookie_password:"+Cookie.get("cookie_password"));
        console.log("cookie_username"+this.getCookie("cookie_username"));
        console.log("cookie_password"+this.getCookie("cookie_username"));
      }

      const url = "performance/customerCurrent.json";
       // const url = "http://127.0.0.1:8000/mcrm/zwebmcrm/com.nbcb.pcrm.mcrm.mobile";
      const data={
        "appId":"mcrm",
        "reqParam":{
          "uid":username.value,
          "pwd":password.value
        },
        "serviceId":"auth.mcrmLogin"
      }
      this.httpService.postData({
        // url:Contents.HTTP_LOCAL_ORIGIN,
        url:url,
        body:data
        // body:{
        //   username:username,
        //   passwprd:password
        // },
      }).then(res=>{
          let result = res.status;
          //alert(result);
          if(result == 0){
            console.log(res.resultRes)
               sessionStorage.setItem("userName",this.login_username);
            // sessionStorage.setItem("userName",JSON.parse(res.resultRes).data.userName);
             //this.navCtrl.push(WelcomeTimePage);
            this.navCtrl.push(TabsPage);
          }
          else{
            //alert("");
            let toast = this.toastCtrl.create({
              message: '',
              duration: 3000,//3
              position: 'middle'//
            })
            toast.present();
          }
        }).catch(error=>{
      })
    }

  }


}



